/* Header styles */
header {
  align-items: center;
  clear: both;
  display: flex;
  height: 100px;
  justify-content: space-between;
  position: absolute;
  width: 100%;
  /* mobile menu styles */ }
  @media screen and (max-width: 992px) {
    header {
      flex-direction: column; } }
  header .logo {
    margin-left: 50px;
    z-index: 20; }
    @media screen and (max-width: 992px) {
      header .logo {
        margin: 5px 0 0 0; } }
  header .toggle-wrapper {
    display: flex;
    flex-direction: column;
    width: 30px;
    height: 26px;
    position: fixed;
    top: 10px;
    left: 30px;
    z-index: 5; }
    header .toggle-wrapper p {
      font-size: 0.7em;
      margin: 32px 0 0 -1px; }
    @media screen and (min-width: 992px) {
      header .toggle-wrapper {
        display: none; } }
  header .menu-toggle {
    cursor: pointer; }
    header .menu-toggle .bg {
      display: block;
      width: 100%;
      height: 30px;
      position: absolute; }
    header .menu-toggle .bar {
      background: #428ade;
      display: block;
      height: 2px;
      position: absolute;
      width: 100%;
      top: 10%; }
      header .menu-toggle .bar:nth-child(2) {
        top: 50%; }
      header .menu-toggle .bar:nth-child(3) {
        top: 90%; }
    header .menu-toggle.active p {
      color: #ffffff; }
    header .menu-toggle.active .bar {
      transition: all 0.3s ease-out; }
      header .menu-toggle.active .bar:first-child {
        background: #ffffff;
        transform: rotate(-45deg);
        top: 50%; }
      header .menu-toggle.active .bar:nth-child(2) {
        opacity: 0; }
      header .menu-toggle.active .bar:nth-child(3) {
        background: #ffffff;
        transform: rotate(45deg);
        top: 50%; }
    header .menu-toggle.inactive .bar {
      background: #3a3a3a;
      transition: all 0.3s ease-out; }
      header .menu-toggle.inactive .bar:first-child {
        background: #3a3a3a;
        transform: rotate(0);
        top: 20%; }
      header .menu-toggle.inactive .bar:nth-child(2) {
        opacity: 1; }
      header .menu-toggle.inactive .bar:nth-child(3) {
        background: #3a3a3a;
        transform: rotate(0);
        top: 80%; }

